<template>
  <section class="rightlist-xxhf">
    <h2>我的消息</h2>
    <div class="timesx">
      <a style="cursor: pointer;" v-bind:class="{active:goReplyActive}" @click="isReply">回复我的</a>
      <a
        style="cursor: pointer;"
        v-bind:class="{active:goInformationActive}"
        @click="isInformation"
      >系统通知</a>
    </div>

    <!-- 有内容时 -->
    <div>
      <!-- 我的消息   回复我的 -->
      <div class="newullist" v-if="isReplyShow">
        <div class="all">
          <ul class="list" v-for="item in ReplyList" :key="item.id">
            <li>
              <div class="img">
                <img v-bind:src="item.rm_img" alt>
              </div>
              <div class="wb">
                <h2>{{item.title}}</h2>
                <div class="bz">
                  <div class="left">
                    <div class="time">{{item.add_time}}</div>
                  </div>
                </div>
              </div>
            </li>
            <li>
              <div class="first">
                <div class="img">
                  <img v-bind:src="item.from_himg" alt>
                </div>
                <div class="wb">
                  <h2>{{item.content}}</h2>
                  <div class="bz">
                    <div class="left">
                      <div class="time">{{item.addtime}}</div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="second">
                <div class="img">
                  <img v-bind:src="item.hf_himg" alt>
                </div>
                <div class="wb">
                  <h2>{{item.hf_content}}</h2>
                  <div class="bz">
                    <div class="left">
                      <div class="time">{{item.hf_time}}</div>
                    </div>
                  </div>
                </div>
              </div>
            </li>
            <li class="ydhd">
              <div class="yd">
                <span></span>
                <span></span>
                <span></span>
              </div>
              <div class="hd">
                <router-link class="a" to="/usercenter/uccollect">回复</router-link>|
                <router-link class="a" to="/usercenter/uccollect">点赞</router-link>
              </div>
            </li>
            <div class="clear"></div>
            <div class="reply" v-if="reply">
              <textarea name placeholder="人可以走，留下你的态度。"></textarea>
              <router-link class="a" to="/usercenter/uccollect">回复</router-link>
            </div>
          </ul>
        </div>
      </div>
      <!-- 我的消息  系统通知 -->
      <ul class="informlist" v-if="isInformationShow">
        <li v-for="item in InformationList" :key="item.id">
          <div class="img">
            <img src="../assets/images/logo.png" alt>
          </div>
          <div class="wb">
            <div class="tit">
              <span v-if='item.is_system==0'>[系统]</span>
                 <span v-if='item.is_system==1'>[系统]</span>
              <h4>{{item.msg_title}}</h4>
              <div class="time">{{item.add_time}}</div>
              <div class="clear"></div>
            </div>
            <p>{{item.msg_content}}
              <br>操作原因：感谢您在评测工场发布您的原创作品。
            </p>
          </div>
        </li>
        <div class="clear"></div>
      </ul>
      <div class="more">
        <a href="javascript:;">查看更多</a>
      </div>
    </div>
    <!-- 无内容时 -->
    <div class="nullsc" v-if="isNone">
      <div class="img">
        <img src="../assets/images/nullsc.png" alt>
      </div>
      <p>您好没有任何消息</p>
    </div>
  </section>
</template>

<script>
export default {
  name: "ucmessage",
  data() {
    return {
      InformationList:[],//系统通知列表
      ReplyList: [],//回复消息列表
      goReplyActive: true, //回复消息
      goInformationActive: false, //系统通知
      isReplyShow: true, //是否显示回复消息
      isInformationShow: false, //是否显示系统通知
      isNone: false, //无内容时
      reply:false //回复
    };
  },
  created() {
    this.getReplyList();
  },
  methods: {
    //显示回复消息
    isReply() {
      (this.goReplyActive = true), (this.isReplyShow = true);
      (this.goInformationActive = false), (this.isInformationShow = false);
      this.getInformationList();
    },
    //显示系统通知
    isInformation() {
      (this.goInformationActive = true), (this.isInformationShow = true);
      (this.goReplyActive = false), (this.isReplyShow = false);
      this.getInformationList();
    },
    // 获取我的消息列表
    getReplyList() {
      this.$axios
        .post("/pcgc/message", {
          action: "comment",
          page: "1"
        })
        .then(res => {
          this.ReplyList = res.data.system_message;
        });
    },
      // 获取系统消息列表
    getInformationList() {
      this.$axios
        .post("/pcgc/message", {
          action: "system",
          page: "1"
        })
        .then(res => {
          this.InformationList = res.data.system_message;
        });
    }
  }
};
</script>
		
<style lang="scss" scoped>
section.rightlist-xxhf {
  margin: 20px 0 60px 14px;
  padding: 0 27px;
  width: 900px;
  min-height: 600px;
  float: left;
  background-color: #fff;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1);
  opacity: 0.93;
  border-radius: 5px;
  h2 {
    font-size: 28px;
    color: #6c6c6c;
    margin: 40px auto 30px auto;
  }
  .timesx {
    height: 40px;
    border-bottom: 1px solid #dcdcdc;
    a {
      font-size: 14px;
      color: #444;
      padding: 0 15px 0 0;
      margin-right: 15px;
      border-right: 1px solid #dcdcdc;
      &:last-child {
        border: none;
      }
    }
    .active {
      color: #ea6457;
    }
  }

  // 回复我的
  .newullist {
    .all {
      .list {
        &:last-child {
          border-bottom: 1px solid #dcdcdc;
        }
        margin-top: 20px;
        padding: 20px 0;
        li {
          height: 112px;
          cursor: pointer;
          display: inline-block;
          vertical-align: middle;
          .img {
            width: 130px;
            height: 112px;
            display: inline-block;
            vertical-align: middle;
            margin-right: 8px;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .sp {
            position: relative;
            .bf {
              position: absolute;
              z-index: 2;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              img {
                width: 100%;
                height: 100%;
              }
            }
          }
          .wb {
            width: 50%;
            display: inline-block;
            vertical-align: middle;
            h2 {
              font-size: 14px;
              line-height: 24px;
              color: #444;
              font-weight: normal;
              margin: 0px auto;
            }
            p {
              font-size: 14px;
              color: #a2a2a2;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
            .bz {
              color: #2e2e2e;
              margin-top: 20px;
              .left {
                display: inline-block;
                .time {
                  display: inline-block;
                  font-size: 14px;
                  color: #929292;
                }
              }
            }
          }
          &:nth-child(1) {
            width: 34%;
            border-right: 1px dashed #dcdcdc;
          }
          &:nth-child(2) {
            padding-left: 40px;
            border-right: 1px dashed #dcdcdc;
            width: 55%;
            .first {
              .img {
                width: 51px;
                height: 51px;
                border-radius: 50%;
                overflow: hidden;
                img {
                  width: 100%;
                  height: 100%;
                }
              }
              .wb {
                width: 60%;
                .bz {
                  margin-top: 0px;
                  .time {
                    font-size: 14px;
                    color: #454545;
                  }
                }
              }
            }
            .second {
              margin: 10px 0 0 51px;
              .img {
                width: 51px;
                height: 51px;
                border-radius: 50%;
                overflow: hidden;
                img {
                  width: 100%;
                  height: 100%;
                }
              }
              .wb {
                width: 60%;
                .bz {
                  margin-top: 0px;
                  .time {
                    font-size: 14px;
                    color: #454545;
                  }
                }
              }
            }
          }
          &:nth-child(3) {
            width: 3%;
            height: 112px;
            position: relative;
            &:hover {
              .hd {
                display: block;
              }
            }
            .yd {
              position: absolute;
              top: 50%;
              right: 0;
              transform: translate(-50%, -50%);
              width: 6px;
              font-size: 3px;
              span {
                width: 6px;
                height: 6px;
                background-color: #ea6457;
                display: inline-block;
                border-radius: 50%;
              }
            }
            .hd {
              width: 97px;
              height: 44px;
              bottom: -9%;
              right: 0;
              display: none;
              position: absolute;
              line-height: 46px;
              text-align: center;
              font-size: 14px;
              background: url(../assets/images/hd.png);
              background-size: 100% 100%;
              a {
                color: #484848;
                padding: 0 5px;
                letter-spacing: 1px;
                &:hover {
                  color: #ea6457;
                }
              }
            }
          }
        }
      }
      .reply {
        width: 882px;
        height: 175px;

        textarea {
          width: 867px;
          height: 100px;
          font-size: 16px;
          font-weight: 400;
          color: #c6c6c6;
          padding: 20px 0 0 15px;
          resize: none;
          outline: none;
          border: none;
          background: rgba(249, 249, 249, 1);
          &:-ms-input-placeholder {
            font-size: 16px;
            color: #c6c6c6;
          }
          &::-webkit-input-placeholder {
            font-size: 16px;
            color: #c6c6c6;
          } /* 使用webkit内核的浏览器 */
          &:-moz-placeholder {
            font-size: 16px;
            color: #c6c6c6;
          } /* Firefox版本4-18 */
          &::-moz-placeholder {
            font-size: 16px;
            color: #c6c6c6;
          } /* Firefox版本19+ */
        }
        a {
          width: 80px;
          height: 30px;
          color: #fff;
          font-size: 16px;
          background-color: #ea6457;
          border-radius: 5px;
          line-height: 30px;
          text-align: center;
          margin: 10px 0 0 0;
          float: right;
        }
      }
    }
  }

  // 系统通知
  ul.informlist {
    margin: 20px 0 0 0;
    li {
      width: 100%;
      padding: 30px 0;
      display: inline-block;
      border-bottom: 1px solid #f2f2f2;

      .img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 20px;
        float: left;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .wb {
        width: 80%;
        float: left;
        .tit {
          span {
            float: left;
            font-size: 14px;
            color: #6c6c6c;
          }
          h4 {
            width: 40%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            float: left;
            font-size: 14px;
            color: #ea6457;
          }
          .time {
            float: left;
            font-size: 14px;
            color: #b6b4b4;
          }
        }
        p {
          font-size: 14px;
          line-height: 20px;
          margin-top: 15px;
          color: #747474;
        }
      }
    }
  }

  .more {
    text-align: center;
    margin: 100px 0 50px 0;
    a {
      width: 346px;
      height: 40px;
      color: #bbb;
      border: 1px solid #cfcfcf;
      border-radius: 5px;
      display: inline-block;
      font-size: 16px;
      line-height: 40px;
      text-align: center;
    }
  }

  .nullsc {
    .img {
      width: 62px;
      height: 62px;
      margin: 50px auto 25px auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    p {
      text-align: center;
      font-size: 16px;
      color: #ccc;
    }
  }
}
</style>





